function sliderManagement() {
    var swiper = new Swiper('.management .swiper', {
        slidesPerView: '1',
        navigation: {
            nextEl: '.management .swiper-button-next',
            prevEl: '.management .swiper-button-prev',
        },
    })
}

function sliderParticipants() {
    var swiper = new Swiper('.participants .swiper', {
        slidesPerView: '5',
        spaceBetween: 30,
        navigation: {
            nextEl: '.participants .swiper-button-next',
            prevEl: '.participants .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: '4',
                spaceBetween: 30,
            },
            1201: {
                slidesPerView: '5',
                spaceBetween: 30,
            }
        }
    })
}

let SlidersArray = []
function sliderNews2() {
    $(".news2__content").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.news2__content[data-slider-id="${index}"] .swiper`, {
            slidesPerView: '4',
            spaceBetween: 24,
            navigation: {
                nextEl: `.news2__content[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.news2__content[data-slider-id="${index}"] .swiper-button-prev`,
            },
            breakpoints: {
                320: {
                    slidesPerView: '2',
                    spaceBetween: 16,
                },
                767: {
                    slidesPerView: '3',
                    spaceBetween: 30,
                },
                1201: {
                    slidesPerView: '4',
                    spaceBetween: 30,
                }
            }
        })
    })
}

function sliderPodcasts() {
    var swiper = new Swiper('.podcasts .swiper', {
        slidesPerView: '2',
        spaceBetween: 24,
        navigation: {
            nextEl: '.podcasts .swiper-button-next',
            prevEl: '.podcasts .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            767: {
                slidesPerView: '2',
            }
        }
    })
}

function sliderApplication() {
    var swiper = new Swiper('.application .swiper', {
        slidesPerView: "auto",
        spaceBetween: 24,
        navigation: {
            nextEl: '.application .swiper-button-next',
            prevEl: '.application .swiper-button-prev',
        }
    })
}

function sliderAdvantages() {
    var swiper = new Swiper('.advantages .swiper', {
        slidesPerView: 1,
        spaceBetween: 24,
        grid: {
            rows: 4,
        },
        navigation: {
            nextEl: '.advantages .swiper-button-next',
            prevEl: '.advantages .swiper-button-prev',
        }
    })
}


$(document).ready(function() {

    sliderManagement();
    sliderParticipants();
    sliderNews2();
    sliderPodcasts();
    sliderApplication();
    sliderAdvantages();


    window.app = {};

    app.clone = {
        data: {
            attr: 'data-clone'
        },

        created() {
            $('[data-clone]').each(this.cloneAndAppend());
        },

        cloneAndAppend() {
            let _this = this;

            return function () {
                let element = $($(this).attr(_this.data.attr));

                if (!element.length) return;

                $(this).append(_this.cloneElement(element));

                $(this).removeAttr(_this.data.attr);
            }
        },

        cloneElement(element) {
            return element.clone(true, true).removeAttr('id').addClass('__cloned')
        }
    }

    app.header = {
        data: {},

        created() {
            $('.header_search').click(function (e) {
                e.preventDefault();

                $('.search-block').addClass('active')
                $('body').addClass('fixed-body')
            })
            $('.search-block_close').click(function (e) {
                e.preventDefault();

                $('.search-block').removeClass('active')
                $('body').removeClass('fixed-body')
            });

            $(window).on('scroll', function () {
                if ($(document).scrollTop() >= $('.section-2').offset().top) {
                    $('.translate').addClass('active')
                } else {
                    $('.translate').removeClass('active')
                }
            })
        }
    };

    app.main = {
        data: {},

        created() {
            this.resize();

            $(window).on('scroll', () => {
                this.resize();
            })
        },
        resize() {
            if ($(document).scrollTop() > 0) {
                $('.section-1_text, .main_bg, .section-1_links, .section-1_image, .section-2, .main_video').addClass('active')
            } else {
                $('.section-1_text, .main_bg, .section-1_links, .section-1_image, .section-2, .main_video').removeClass('active')
            }
        }
    };

    app.searchBlock = {
        data: {},

        created() {
            $('.search-block_categories a').click(function (e) {
                e.preventDefault();

                $('.search-block_categories a, .search-block_block').removeClass('active')
                let block = $('[data-id="' + $(this).attr('href') + '"]');
                $(this).addClass('active')
                block.addClass('active')
            });

            $('.search-block_categories a').each(function () {
                let block = $('[data-id="' + $(this).attr('href') + '"]');
                $(this).after(block.clone(true, true).addClass('__clone'))
            })
        }
    };

    app.section2 = {
        data: {},

        created() {
            setTimeout(() => {
                this.showTitle();
            }, 5000)
        },

        showTitle() {
            $('.section-2_item-value').fadeOut(300, function () {
                $('.section-2_item-title').fadeIn(300);
            });

            setTimeout(() => {
                this.showValue();
            }, 5000)
        },

        showValue() {
            $('.section-2_item-title').fadeOut(300, function () {
                $('.section-2_item-value').fadeIn(300);
            });

            setTimeout(() => {
                this.showTitle();
            }, 5000)
        }
    };

    jQuery(function () {
        for (let module in app) {
            app[module].created();
        }
    });


    // Tabs
    $(".tabs__btn").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tabs__btn").removeClass("tabs__btn--active")
        $(this).addClass("tabs__btn--active")

        $(this).parents(".tabs").find(".tabs__desc").removeClass("tabs__desc--active")
        $(this).parents(".tabs").find(`.tabs__desc[data-content-path="${path}"]`).addClass("tabs__desc--active")
    })

    // Tabs2
    $(".participation__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".participation__tab").removeClass("participation__tab--active")
        $(this).addClass("participation__tab--active")

        $(this).parents(".participation__inner").find(".participation__desc").removeClass("participation__desc--active")
        $(this).parents(".participation__inner").find(`.participation__desc[data-content-path="${path}"]`).addClass("participation__desc--active")
    })

    // Tabs3
    $(".mediabank__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".mediabank__tab").removeClass("mediabank__tab--active")
        $(this).addClass("mediabank__tab--active")

        $(this).parents(".mediabank__inner").find(".mediabank__cards").removeClass("mediabank__cards--active")
        $(this).parents(".mediabank__inner").find(`.mediabank__cards[data-content-path="${path}"]`).addClass("mediabank__cards--active")
    })

    // Tabs4
    $(".works__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".works__tab").removeClass("works__tab--active")
        $(this).addClass("works__tab--active")

        $(this).parents(".works__inner").find(".works__content").removeClass("works__content--active")
        $(this).parents(".works__inner").find(`.works__content[data-content-path="${path}"]`).addClass("works__content--active")
    })

    // Tabs5
    $(".news2__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".news2__tab").removeClass("news2__tab--active")
        $(this).addClass("news2__tab--active")

        $(this).parents(".news2__inner").find(".news2__content").removeClass("news2__content--active")
        $(this).parents(".news2__inner").find(`.news2__content[data-content-path="${path}"]`).addClass("news2__content--active")
    })


    // Mask-tel
    $(".form-tel").mask("+7 (999)-999-99-99");

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Select
    $(".custom_select select").niceSelect()

    // Play
    $('.play').click(function () {
        $(this).addClass('play--active')
        $(this).siblings('.video')[0].play();
    })

    $('.video').click(function () {
        $(this).siblings('.play').toggleClass('play--active')
    })

    // Detailed
    $('.detailed').click(function () {
        $(this).addClass('detailed--active')
        $(this).siblings('.hide').addClass('hide--active')
        $(this).siblings('.hide-btn').addClass('hide-btn--active')
    })

    $('.hide-btn').click(function () {
        $(this).removeClass('hide-btn--active')
        $('.detailed').removeClass('detailed--active')
        $('.hide').removeClass('hide--active')
    })

})

